.page{
    background-color: #13293d;
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;    
}

.container {
    width: 90%;
    max-width: 450px;
    background-color: #1b98e0;
    padding: 1rem 10px;
    color: #eee;
    border-radius: 10px;
}

.name{
    color: #e8f1f2;
    font-size: 2.5rem;
    font-family: poppins;
    font-weight: bolder;
}

.inputs{
    color: #e8f1f2;
    width: 100%;    
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.buttons {
    margin-top: 10px;
    display: flex;
    justify-content: center;
}

#err{
    animation: err .2s ;
}

.nameCont {
    display: flex;
    justify-content: space-between;
}

.img {
    height: 80px;
    margin-right: 1rem;
}

@keyframes err {
    0%{
        transform: translateX(0);
    }
    25%{
        transform: translateX(0.5rem);

    }
    50%{
        transform: translateX(0rem);

    }
    75%{
        transform: translateX(-0.5rem);
    }
    100%{
        transform: translateX(0);

    }
    
}