.info {
    display: grid;
    grid-template-columns: 200px 90px 90px 250px 150px 200px 180px 200px 30px 30px;
    color: #e8f1f2;
    padding: 10px 0;
    transition: all .3s;
    transition: background 1s;
    align-items: center;
    width: calc(200px + 90px + 90px + 250px + 150px + 200px + 180px + 200px + 30px + 30px);
}

.info:hover{
    background-color: #00000033;
}

.btn svg{
    transition: all .3s;
}

.btn svg:hover{
    transform: scale(1.2);
    cursor: pointer;
}

#del{
    background-color: #ee000066;
}