.btn{
    padding: 10px 20px;
    color: #e8f1f2;
    font-weight: bold;
    background: none;
    border: 2px solid #e8f1f2;
    border-radius: 10px;
    transition: all .3s;
}

.btn:hover {
    color: #000;
    background-color: #e8f1f2;
    transform: scale(1.15);
    cursor: pointer;
}