.header{
    width: 100%;
    height: 50px;
    background-color: #1b98e0;
    display: flex;
    justify-content: center;
}

.container {
    width: 100%;
    height: 50px;
    background-color: #1b98e0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 1rem;
}

.btn{
    cursor: pointer;
}

.btn svg {
    transition: all .3s;

}

#active svg {
    transform: rotateZ(45deg);
    
}