.page{
    width: 100%;
    height: 100%;
    overflow-x: hidden;
}

.header {
    display: grid;
    grid-template-columns: 200px 90px 90px 150px 200px 300px 200px 200px 30px 30px;
    font-weight: bold;
    color: #e8f1f2;
    padding: 10px 0;
}

.header::after{
    display: flex;
    width: calc(200px + 90px + 90px + 150px + 200px + 300px + 200px + 200px + 30px + 45px);
    /* width: 100%; */
    height: 1px;
    background-color: #e8f1f2;
    margin-top: 5px;
    content: '';
    margin-bottom: 0;
    margin-left: -15px;
}

.infos {
    width: 100%;
    display: block;
    flex-direction: column;
    align-items: center;
    /* border-top: 1px solid #e8f1f2; */
}

.erro{
    color: #e8f1f277;
    font-weight: bolder;
    font-size: 1.5rem;
    text-align: center;
}