.page{
    width: 100%;
    height: 100%;
    overflow-x: hidden;
}

.header {
    display: grid;
    font-weight: bold;
    color: #e8f1f2;
    padding: 10px 0;
}

.after{
    display: flex;
    /* width: calc(200px + 90px + 90px + 150px + 200px + 180px + 200px + 30px + 45px); */
    /* width: 100%; */
    min-width: 100%;
    height: 1px;
    background-color: #e8f1f2;
    margin-top: 5px;
    content: '';
    margin-bottom: 0;
    margin-left: -15px;
}

.infos {
    width: 100%;
    display: block;
    flex-direction: column;
    align-items: center;
    /* border-top: 1px solid #e8f1f2; */
}
.erro{
    color: #e8f1f277;
    font-weight: bolder;
    font-size: 1.5rem;
    text-align: center;
}


.info {
    display: grid;
    /* grid-template-columns: 200px 90px 90px 150px 200px 180px 200px 30px 30px; */
    color: #e8f1f2;
    padding: 10px 0;
    transition: all .3s;
    transition: background 1s;
    align-items: center;
    /* width: calc(200px + 90px + 90px + 150px + 200px + 180px + 200px + 30px + 30px); */
}

.info:hover{
    background-color: #00000033;
}

.btn svg{
    transition: all .3s;
}

.btn svg:hover{
    transform: scale(1.2);
    cursor: pointer;
}

#del{
    background-color: #ee000066;
}