.nav {
    width: 180px;
    z-index: 999;
    height: 100%;
    background-color: #1b98e0;
    transition: all .3s;
    overflow-y: auto;
    font-size: 14px;
    display: flex;
}

.nav::-webkit-scrollbar{
    background: none ;
    width: 3px;
}

.nav::-webkit-scrollbar-thumb{
    background-color: #e8f1f288;
}

.links{
    display: flex;
    flex-direction: column;
    width: 100%;
}

#disable {
 transform: translateX(-180px);
 width: 0px;
}