.page {
    background-color: #13293d;
    width: 100%;
    height: 100vh;
}

.container {
    display: flex;
    width: 100%;
    height: calc(100vh - 50px);
    overflow: hidden;
}

.content {
    height: calc(100vh -50px);
    width: 100%;
    transition: all .3s;
}

.content#active{
    width: calc(100% - 150px);
    
}