.container{
    width: 100%;
    height: 100%;
    background-color: #00000022;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    padding: 10px;
    max-width: 100vw;
}

.title {
    color: #e8f1f2;
    font-family: Poppins;
    text-align: center;
    font-size: 2rem;
}

.content {
    width: 100%;
    height: 100%;
    border: 1px solid #e8f1f2;
    display: block;
    flex-direction: column;
    align-items: center;
    /* max-width: calc(100% - 10px); */
    overflow: auto;
    box-sizing: border-box;
    padding-left: 10px;
}

.content::-webkit-scrollbar{
    background: none;
    height: 3px;
    width: 3px;
}

.content::-webkit-scrollbar-thumb{
    background: #e8f1f2;
    border-radius: 5px;
}

.content::-webkit-scrollbar-button{
    background-color: none;
    width: 0px;
    height: 0px;
}

.content::-webkit-scrollbar-corner{
    width: 0px;
    display: none;
    height: 0px;
}

.nav {
    display: flex;
    margin: 10px auto;
    gap: 5px;
}

.navOpt{
    border: solid 1px #e8f1f2;
    width: 20px;
    height: 20px;
    color: #e8f1f2;
    align-items: center;
    justify-content: center;
    display: flex;
    border-radius: 50%;
    cursor: pointer;
    transition: all .3s;
    text-decoration: none;
}

.navOpt:hover{
    transform: scale(1.1);
}

.navOpt#active {
    background-color: #1b98e0;
    border-color: #1b98e0;
}

.footer {
    display: flex;
    align-items: center;
    justify-content: space-between;

    /* display: grid;
    grid-template-columns: 1fr 10fr 1fr;
    width: 100%; */
    
    max-width: 100%;
    box-sizing: border-box;
    /* margin: 0 10px; */
}

.btns {
    display: flex;
    gap: 5px;
    display: flex;
    justify-content: end;
}

.btns div {
    transition: all .3s;
    display: flex;
    align-items: center;
}

.btns div:first {
    width: 55px;
}

.btns div:hover svg {
    transform: scale(1.1);
    cursor: pointer;
}